@charset "UTF-8";
@media (max-width: 768px) {
  .fs-l {
    font-size: calc(24px + 20.4 * (100vw - 430px) / 1920);
  }
  .fs-m {
    font-size: calc(18px + 30.6 * (100vw - 430px) / 1920);
  }
  .fs-s {
    font-size: calc(16px + 27.2 * (100vw - 430px) / 1920);
  }
  .fs-xs {
    font-size: calc(14px + 6.8 * (100vw - 430px) / 1920);
  }
  .fs-xxs {
    font-size: calc(12px + 10.2 * (100vw - 430px) / 1920);
  }
  .fs-xss {
    font-size: calc(10px + 13.6 * (100vw - 430px) / 1920);
  }
}
.news-block__info-wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: auto;
  padding-top: 8px;
  border-top: 1.5px solid #F6F6F6;
}
.news-block__opinion {
  display: inline-flex;
  align-items: center;
  gap: 9px;
  margin-right: 26px;
}
.news-block__opinion::before {
  content: "";
  width: 18px;
  height: 18px;
}
.news-block__opinion--positive {
  color: #3075FC;
}
.news-block__opinion--positive::before {
  background: center/cover no-repeat url("../image/smile.svg");
}
.news-block__opinion--negative {
  color: #F91818;
}
.news-block__opinion--negative::before {
  background: center/cover no-repeat url("../image/sad.svg");
}
.news-block__heart {
  min-width: 16px;
  min-height: 16px;
  background: center/cover no-repeat url("../image/heart.svg");
}

@media (min-width: 1200px) {
  .news-block__opinion::before {
    width: 24px;
    height: 24px;
  }
  .news-block__heart {
    min-width: 26px;
    min-height: 26px;
  }
}
.filter-buttons {
  display: flex;
  gap: 5px;
  padding: 6px 9px;
  box-sizing: border-box;
  border-radius: 60px;
}
.filter-buttons__btn {
  flex-grow: 1;
  padding: 10px 25px;
  text-align: center;
  border-radius: 60px;
  background-color: rgba(0, 0, 0, 0);
  transition: 0.15s ease-in-out;
}
.filter-buttons__btn--active {
  color: #FFFFFF !important;
  font-weight: 700;
  background-color: #3075FC;
}
.filter-buttons--outline .filter-buttons__btn {
  border: 1px solid #3075FC;
}

@media (min-width: 1200px) {
  .filter-buttons__btn {
    font-size: 14px;
  }
}
.stylist-consultation {
  width: 100%;
  min-height: 463px;
  padding: 26px 16px;
  color: #FFFFFF;
  background: linear-gradient(0deg, rgba(0, 0, 0, 0.3) 0%, rgba(0, 0, 0, 0.3) 100%), left/cover url("../image/stylist-consultation bg.png"), lightgray 50%/cover no-repeat;
  box-sizing: border-box;
}
.stylist-consultation__title-wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.stylist-consultation__title {
  display: inline-block;
  font-weight: 700;
  font-size: calc(24px + 0.625vw);
}
.stylist-consultation__info-btn {
  display: inline-block;
  width: 20px;
  height: 20px;
  margin-top: 5px;
  margin-left: auto;
  background: center/contain no-repeat url("../image/info.svg");
}
.stylist-consultation__price {
  margin-top: 6px;
  font-weight: 800;
}
.stylist-consultation__price::after {
  content: " ₽";
}
.stylist-consultation__steps-wrapper {
  display: flex;
  flex-direction: column;
  gap: 30px;
  margin-top: 40px;
  counter-reset: num;
}
.stylist-consultation__step {
  display: flex;
}
.stylist-consultation__step-info {
  max-width: 140px;
  font-size: 12px;
  line-height: 14.09px;
}
.stylist-consultation__step-title {
  display: block;
  margin-bottom: 3px;
  font-weight: 700;
}
.stylist-consultation__step-title::after {
  content: "Шаг " counter(num);
}
.stylist-consultation__step::before {
  content: counter(num);
  counter-increment: num;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 39px;
  height: 39px;
  margin-right: 19px;
  font-weight: 300;
  font-size: 20px;
  background: center url("../image/border.svg") no-repeat;
}
.stylist-consultation__btn {
  display: inline-block;
  margin-top: 40px;
  padding: 11px 29px;
  border-radius: 4px;
  font-weight: 700;
  text-transform: uppercase;
  background: linear-gradient(89.08deg, #3075FC 0%, #A230FC 93.22%);
}

@media (min-width: 1200px) {
  .stylist-consultation {
    min-height: 762px;
    display: grid;
    grid-auto-columns: 420px;
    grid-template-rows: min-content;
    align-content: end;
    align-items: end;
    justify-content: space-between;
    justify-items: start;
    padding: 0 100px 68px 100px;
  }
  .stylist-consultation__steps-wrapper {
    margin-top: auto;
    grid-column: 2/3;
    grid-row: 1/5;
  }
  .stylist-consultation__left-panel-wrapper {
    grid-row: 1/4;
  }
  .stylist-consultation__btn {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    max-height: 70px;
    font-size: 24px;
  }
  .stylist-consultation__step-info {
    max-width: unset;
    font-size: 20px;
    line-height: normal;
  }
  .stylist-consultation__step-title {
    font-size: 24px;
    margin-bottom: 12px;
  }
  .stylist-consultation__step::before {
    width: 94px;
    height: 94px;
    margin-right: 46px;
    background-size: contain;
    font-size: 47px;
  }
}
.nav__search-with-filter {
  display: flex;
  gap: 10px;
}
.nav__search-with-filter > .nav__search-wrapper {
  flex: 1 1;
}
.nav__search-wrapper {
  position: relative;
  display: flex;
  align-items: stretch;
  z-index: 2;
  min-height: 40px;
  padding-right: 8px;
  border-radius: 40px;
  box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.0509803922);
}
.nav__search {
  display: inline-flex;
  align-items: center;
  width: 100%;
}
.nav__search-icon {
  display: inline-block;
  width: 16px;
  height: 16px;
  background: center url("../image/search.svg") no-repeat;
}
.nav__search-input {
  width: 100%;
  height: 100%;
}
.nav__btns-wrapper {
  display: flex;
  align-items: center;
}
.nav__color-btn {
  width: 24px;
  height: 24px;
  color: rgba(0, 0, 0, 0);
  position: relative;
  border-radius: 46px;
}
.nav__color-btn::after {
  content: "";
  width: 18px;
  height: 18px;
  margin: 3px;
  background-color: currentColor;
  position: absolute;
  top: 0;
  left: 0;
  border-radius: 46px;
}
.nav__color-selector {
  position: absolute;
  top: 50%;
  left: 0;
  right: 0;
  padding: 30px 16px 26px 16px;
  z-index: -1;
  border-radius: 0px 0px 20px 20px;
  background: #FFFFFF;
  box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.05);
}
.nav__color-selector--hidden {
  display: none;
}
.nav__colors-btns-wrapper {
  display: flex;
  gap: 10px;
}
.nav__search-counter-wrapper {
  position: relative;
}
.nav__search-counter {
  width: 55px;
  height: 24px;
  padding: 4px 4px 4px 9px;
  margin-left: 7px;
  border-radius: 50px;
  appearance: textfield;
}
.nav__search-counter-btn {
  position: absolute;
  right: 0;
  width: 10px;
  height: 10px;
  padding: 0 10px;
  padding-top: 13px;
  background: bottom url("../image/mini-top-arrow.svg") no-repeat;
}
.nav__search-counter-btn#dec-count {
  bottom: 0;
  background: top url("../image/mini-down-arrow.svg") no-repeat;
}
.nav__links-wrapper {
  display: flex;
  flex-wrap: wrap;
  gap: 5px;
}
.nav__link {
  display: inline-block;
  border-radius: 50px;
}

.main__feedback-sections {
  background-color: #f5f6f6;
}
.main__primary-slider {
  margin-top: 3px;
}
.main__primary-slider-img {
  max-width: 100%;
}
.main__slider-banner {
  margin: 0 25px;
  margin-top: -50px;
  z-index: 1;
}
.main__slider {
  padding: 0 14px;
}
.main__info-slider {
  margin-top: 52px;
  overflow-y: unset;
}
.main__logo-slider {
  margin-top: 46px;
  margin-bottom: 45px;
  padding-bottom: 37px;
}
.main__logo-slider .swiper-wrapper {
  align-items: center;
}
.main__logo-slider .slider__slide {
  padding-right: 43px;
  border-radius: 0;
}
.main__logo-slider .slider__slide:last-child {
  padding-right: 0;
}
.main__trends-slider {
  margin-bottom: 50px;
}
.main__filter-buttons {
  margin: 0 10px;
  margin-bottom: 17px;
}
.main__finished-image {
  display: flex;
  flex-direction: column;
  padding: 17px 15px 20px 15px;
  background-color: #F6F6F6;
  box-shadow: inset 0px 10px 15px 0px rgba(0, 0, 0, 0.1019607843);
}
.main__finished-image .slider {
  margin-top: 17px;
}
.main__about-payment {
  margin-top: 43px;
}
.main__brand-baner {
  min-height: 190px;
  margin: 13px 18px 0 15px;
}
.main__brand-baner .banner__wrapper {
  padding: 14px 17px 23px 17px;
}

@media (min-width: 1200px) {
  .header {
    margin: 0;
  }
  .header__logo-and-basket-wrapper, .header__breadcrumbs, .header > .nav {
    margin-left: 100px;
    margin-right: 100px;
  }
  .main__slider-banner .banner__title {
    font-size: 40px;
  }
  .main__slider-banner .banner__block-wrapper {
    font-size: 24px;
  }
  .main__primary-slider, .main__block-title, .main__slider, .main__finished-image, .main__brand-baner {
    margin-left: 0;
    margin-right: 0;
    padding-left: 100px;
    padding-right: 100px;
  }
  .main__primary-slider {
    margin-top: 37px;
  }
  .main__primary-slider > .swiper-wrapper > .swiper-slide {
    width: auto;
  }
  .main__primary-slider > .slider__pagination {
    display: none;
  }
  .main__finished-image {
    padding-top: 82px;
  }
  .main__slider-banner {
    margin-left: 20%;
    margin-right: 20%;
    margin-top: -171px;
  }
  .main__filter-buttons {
    margin-left: 100px;
    margin-right: 100px;
  }
  .main__feedback-sections {
    background-color: unset;
  }
  .main__logo-slider .slider__slide {
    height: 50px;
  }
  .main__logo-slider .slider__slide a, .main__logo-slider .slider__slide img {
    height: 100%;
  }
  .main__logo-slider .slider__slide a {
    display: flex;
    align-items: center;
  }
  .main__about-payment {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-left: 100px;
    padding-right: 100px;
  }
  .main__about-payment > .about-payment__text-wrapper, .main__about-payment > .about-payment__cards-wrapper {
    flex: 1;
  }
  .main__about-payment > .about-payment__text-wrapper {
    max-width: 683px;
    font-size: 20px;
  }
  .main__about-payment > .about-payment__text-wrapper > .about-payment__title {
    font-size: 36px;
  }
  .main__about-payment > .about-payment__cards-wrapper {
    display: flex;
    justify-content: flex-end;
  }
  .main > .stylist-consultation {
    margin-top: 32px;
  }
}